body {
  box-sizing: border-box;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hidden {
  display: none !important;
}

.selected {
  color: #66c8ed;
}

a {
  color: #999999;
}

a:hover {
  color: #66c8ed;
  text-decoration: none;
}
/* ====Navigation=== */

.logo-image {
  margin-right: 5px;
}
.navbar{
  background-color: #e5e5e5!important;
}
.navbar-nav {
  align-items: center;
}
.navbar-nav > a {
  margin-right: 10px;
}
.nav-link-img-container {
  display: flex;
  align-items: center;
}
.nav-link-img-container p {
  display: inline-block;
  padding-top: 17px;
}
.icon-monster {
  width: 13px;
  height: auto;
}
.fas {
  margin-right: 1px;
  font-size: 0.9rem;
}
.user-avatar {
  width: 30px;
  height: 30px;
  margin-right: 3px;
}

/* ====LogIn=== */
.logIn-form {
  max-width: 480px;
  text-align: center;
  transform: translateY(calc(50%));
  margin: 0 10px;
}
.card-title {
  background-color: #f7f7f7;
  padding: 10px 0;
  border: 1px solid #f7f7f7;
  box-shadow: 0px 3px 5px 1px rgba(232, 229, 232, 1);
}
.card-title h6 {
  opacity: 0.7;
}
.logo-img-container {
  width: 80px;
  height: auto;
  padding: 10px 0 30px 0;
}

.submit-form .btn-primary {
  background-color: #66c8ed;
  border-color: #66c8ed;
}
.submit-form .btn-primary:hover,
.submit-form .btn-primary:active {
  background-color: #1bafe4;
}
.select-user {
  margin-bottom: 10px;
}

/* ====Home=== */

.home {
  max-width: 576px;
  margin: 20px auto;
}
.home-links-container {
  justify-content: center;
}

.nav .nav-link {
  color: #66c8ed;
}

.nav .nav-link.active {
  color: #495057;
  background-color: #fff;
  border-color: #dee2e6 #dee2e6 #fff;
}

/* ====Question=== */

.user {
  width: 90%;
  margin: 20px auto;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.user-name {
  background-color: #f7f7f7;
  padding: 7px 0 7px 20px;
}
.user-name h5 {
  margin: 0;
}
.user-info-container {
  display: flex;
  width: 90%;
  margin: 0 auto;
  padding: 20px 0;
}

.image-container {
  width: 130px;
  height: auto;
  text-align: center;
  padding: 5px;
}

.image-container img {
  width: 100%;
}
.question-container {
  width: 350px;
  padding-top: 7px;
}
.btn-outline-info {
  color: #66c8ed;
  border-color: #66c8ed;
}
.btn-outline-info:hover {
  background-color: #66c8ed;
}

/* ====Poll=== */
.poll {
  max-width: 480px;
}

.user-info-container-poll {
  display: flex;
  width: 90%;
  margin: 0 auto;
  padding: 20px 0;
}

.poll-first-question-container,
.poll-second-question-container {
  display: flex;
}

.poll-second-question-container {
  margin-bottom: 20px;
}

.poll-image-container {
  width: 172px !important;
}

#first-question-checkbox,
#second-question-checkbox {
  width: 15px;
  height: 15px;
  color: #000;
}
.input-container {
  margin-right: 10px;
}
.input-container,
.p-container {
  display: table;
}
.input-container input,
.p-container p {
  display: table-cell;
  vertical-align: middle;
}

/* ====PollResults=== */
.pollresults-container-poll {
  align-items: center;
  padding: 10px 0;
  display: flex;
  width: 93%;
  margin: 0 auto;
}
.pollresults-image-container {
  display: table;
  width: 200px;
  height: auto;
  padding-bottom: 40px;
  margin-right: 15px;
}
.pollresults-image-container img {
  display: table-cell;
  width: 100%;
}
.pollresults {
  width: 400px;
}
.pollresults-title {
  margin-bottom: 12px;
  font-size: 1.5rem;
}
.pollresults-container {
  position: relative;
  padding: 20px;
  height: 130px;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  margin-bottom: 20px;
}
.votes {
  text-align: center;
}
.votes h6 {
  font-size: 0.8rem;
}
.pollresults-question {
  height: 40px;
  margin-bottom: 20px;
}
.pollresults button {
  margin-bottom: 10px;
}
.select {
  background-color: rgb(102, 200, 237, 0.5);
}
.voted-container {
  position: absolute;
  right: -23px;
  top: -17px;
  display: table;
  width: 50px;
  height: 50px;
}
.voted {
  width: 100%;
  height: 100%;
  color: #fff;
  border: 1px solid #ffc107;
  border-radius: 50%;
  display: table-cell;
  vertical-align: middle;
}

/* ====UserStats=== */

.userStats-container {
  max-width: 480px;
  margin: 20px auto;
  padding: 15px 0;
  display: flex;
  justify-content: space-around;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.userStats-avatar {
  width: 130px;
  height: auto;
  text-align: center;
  padding: 10px;
  border-right: 1px solid #e5e5e5;
  margin-left: -17px;
}
.userStats-avatar img {
  width: 100%;
}
.userStats-statistic {
  display: table;
  height: 135px;
  width: 200px;
  margin-left: -10px;
  padding-right: 25px;
  border-right: 1px solid #e5e5e5;
}
.userStats-statistic-text {
  display: table-cell;
  vertical-align: middle;
}
.userStats-statistic-text h5 {
  font-size: 1.5rem;
  margin-bottom: 15px;
}
.userStats-statistic-text h6 {
  font-size: 0.9rem;
}
.answered-questions,
.created-questions {
  display: flex;
  justify-content: space-between;
  padding: 5px 0;
}
.answered-questions {
  border-bottom: 2px solid #e5e5e5;
}
.created-questions {
  margin-top: 4px;
}
.userStats-score {
  display: flex;
  flex-direction: column;
  width: 80px;
  height: 100px;
  text-align: center;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  margin-right: 20px;
  margin-top: 19px;
  background-color: #d9d9d9;
}
.userStats-score-text {
  padding-top: 5px;
  border-bottom: 1px solid #fff;
  color: #fff;
}
.userStats-score-number {
  display: table;
  width: 45px;
  height: 45px;
  margin: 10px auto;
  background-color: #66c8ed;
  border: 1px solid #fff;
  color: #fff;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}
.userStats-score-number p {
  display: table-cell;
  vertical-align: middle;
}

/* ====CreateQuestion=== */
.createQuestion {
  max-width: 480px;
  margin: 20px auto;
}

.card-header {
  text-align: center;
}

.createQuestion hr::after {
  content: "OR";
  position: relative;
  top: -13px;
  left: 45%;
  display: inline-block;
  padding: 0 0.25em;
  background-color: #fff;
}

.createQuestion-form h6 {
  margin-bottom: 20px;
}
.createQuestion-form .form-group {
  margin: 1.2rem 0;
}

/* ====NoMatch 404=== */

.error-page{
  margin:120px auto;
  width: 375px;
}

/* ====Media Queries=== */

@media screen and (max-width: 576px) {
  .logIn-form {
    width: 90%;
    transform: translateY(calc(30%));
    -webkit-transform: translateY(calc(30%));
    -moz-transform: translateY(calc(30%));
    -ms-transform: translateY(calc(30%));
    -o-transform: translateY(calc(30%));
  }
  .navbar-nav {
    align-items: flex-start;
  }
  .navbar-nav > a {
    margin-top: 10px;
  }
  .card-title h5 {
    font-size: 1.1rem;
  }

  .user {
    width: 100%;
  }
  .poll {
    width: 90%;
  }
  .question-container h5 {
    font-size: 1.1rem;
  }
  .createQuestion {
    width: 90%;
  }
  .userStats-container {
    width: 90%;
  }
  .userStats-statistic-text h5 {
    font-size: 1.2rem;
  }
  .userStats-statistic-text h6 {
    font-size: 0.8rem;
  }
  .userStats-statistic {
    height: 100px;
    margin-left: 10px;
    padding-right: 11px;
  }
  .userStats-avatar {
    padding: 3px;
    margin-left: 0px;
  }
  .userStats-score {
    margin: 6px 5px 0 5px;
  }
}
